import React from 'react';
import Button from '../button/Button';
import { VscGithub } from 'react-icons/vsc';

import scss from './loginbutton.module.scss';
import { logIn } from '../../store/reducers/auth/github';

const LoginButton = () => {
  const handleLoginClick = () => {
    const params = new URLSearchParams({
      client_id: 'c521e570809667490a12',
      redirect_uri: `${window.location.origin}/login`,
      scope: 'repo,read:user,write:discussion,read:discussion',
      state: 'teste123',
      allow_signup: true,
    }).toString();
    console.log(params);

    // window.open(url, '_blank');
    // 'https://github.com/login/oauth/authorize'
    // signIn
  };

  return (
    <div className={scss._root}>
      <Button onClick={handleLoginClick} color="black" type="outline">
        <VscGithub /> Entrar com GitHub
      </Button>
    </div>
  );
};

export default LoginButton;
